.timetables{
	display: block;

	header{
		margin-bottom:40px;

		h1{
			margin-bottom:20px;
		}

		.pdf_dl {
			display: none;
			@media (min-width: $screen-md) {
				display: inline-block;
				float: right;
				margin: 10px 20px 0 0;
			}
		}

		.gymnav{
			display: inline-block;
			width:250px;
			height:40px;
			position: relative;
			background: #323232 url(../images/select_arrow.png) no-repeat right 10px center;
			color:white;
			z-index:9;

			.current{
				display: block;
				with:100%;
				height:40px;
				line-height:40px;
				padding:0 20px;
			}

			ul{
				display: none;
				width:100%;
				top:100%;
				position: absolute;
				background: white;
				-webkit-box-shadow: 0px 3px 5px 2px rgba(0,0,0,0.09);
				-moz-box-shadow: 0px 3px 5px 2px rgba(0,0,0,0.09);
				box-shadow: 0px 3px 5px 2px rgba(0,0,0,0.09);

				li{
					display: block;

					a{
						padding: 5px 20px;
						display: block;
						color: #323232;
					}
				}
			}

			&:hover{
				ul{
					@media (min-width: $screen-xs) {
						display: block;
					}
				}
			}

			&.open{
				ul{
					display: block;
				}
			}
		}
	}

	&__container{
		display: grid;
		grid-template-columns: 1fr;
    	//grid-template-rows: 50px 50px;
		//background: $yellow1;
		padding: 0 2px;
		overflow:hidden;

		@media (min-width: $screen-xs) {
			grid-template-columns: 1fr 1fr;
		}
		@media (min-width: 700px) {
			grid-template-columns: 1fr 1fr 1fr 1fr;
		}
		@media (min-width: $screen-md) {
			grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
		}

		.row{
			display: block;

			.heading{
				display: block;
				background:white;
				margin-bottom:4px;
				text-transform: uppercase;
				font-size: 16px;
				text-align: Center;
				padding: 10px;
				width: calc(100% + 10px);
    			margin-left: -5px;
    			position: relative;
			}

			&:last-child{
				.heading{
					&:after{
						height: 100%;
						width: 700%;
						left: 100%;
						top: 0;
						background: #fff;
						content: '';
						display: block;
						position: absolute;
					}
				}
			}

			.item{
				display: block;
				background: white;
				margin:auto;
				width:calc(100% - 4px);
				margin-bottom:4px;
				    -webkit-box-shadow: 0 5px 14px -3px rgba(0,0,0,.18);
    -moz-box-shadow: 0 5px 14px -3px rgba(0,0,0,.18);
    box-shadow: 0 5px 14px -3px rgba(0,0,0,.18);

				.top{
					background: #323232;
					padding:4px;
					text-align:center;

					h3{
						color:$yellow1;
						font-size:16px;
						text-transform:uppercase;
					}
					span{
						color:$yellow1;
					}
				}
				.bottom{
					padding:4px;
				}
				.popup{
					width:20px;
					height:20px;
					display: block;

					img{
						width:100%;
						height:auto;
					}
				}
			}
		}
	}
}

.modals_container{
	display:none;
}

.mfp-auto-cursor .mfp-content{
	width:600px;
	max-width:90%;
	padding:30px;
	border-radius:10px;
	background:white;
	position: relative;
	margin:auto;

	h4{
		margin-bottom:20px;
	}
}

.modal_content {
	.instructor {
		text-decoration: underline;
		text-transform: uppercase;
	}
}

.timetables_page{
	.grid {
		display: grid;

		&.main_content {
			//grid-template-columns:repeat(auto-fit, minmax(700px, 1fr));

		}

	}
}