.post_content {
	.section_title {
		p {
			font-size: 18px;
			@include aniverse_bold;
		}
   
	}

	img{
		max-width:100%;
		height:auto;
	}

	a{
		color:black;
	}

	ul {
	  margin: 0 0 20px 20px;

	  li {
	      list-style: disc outside none;
	      font-size: 18px;
	    }
	}

	ol {
	margin: 0 0 20px 20px;

	    li{
	        list-style: decimal outside;
	        font-size: 18px;
	    }

	}

	h5 {
		padding: 0 0 31px;
	}

	p {
		font-size: 18px;
		padding: 0 0 25px;
	}

	.alignleft {
		float: left;
		margin: 0 10px 25px 0;
		max-width: 100%;
		@media (min-width: $screen-sm) {
			margin: 0 25px 25px 0;
		}
	}
	.alignright {
		float: right;
		margin: 0 0 25px 10px;
		max-width: 100%;
		@media (min-width: $screen-sm) {
			margin: 0 0 25px 25px;
		}
	}
}