.banner {
	&.spec1 {
		.row {
			max-width: 1260px;
		}
		.grid {
			display: grid;
			grid-template-areas:
		      "banner_left"
		      "banner_right";
		    grid-template-columns: 1fr;
		    grid-template-rows: auto;

			  @media (min-width: $screen-sm) {
			    grid-template-areas:
			    "banner_left banner_right";
			  	grid-template-columns: 2fr minmax(300px, 1fr);
			  	grid-template-rows: auto 1fr;
			  	grid-column-gap: 2px;
			 
			}
			.col_left {
				padding: 75px 30px;
				display: flex;
				justify-content: center;
				flex-direction: column;
				grid-area: banner_left;

				@media (min-width: $screen-sm) {
					padding: 0 80px;
				}

				h1 {
				    @include aniverse_bold;
				    background-color: $yellow1;
				    clear: both;
				    color: #000;
				    float: left;
				    font-size: 38px;
				    margin: -1px 0;
				    padding: 5px 7px;
				    position: relative;
				    text-transform: uppercase;
				    width: auto;

				    &:nth-child(2n) {
						left: 20px;
				    }
				}

				.button_container {
					margin: 33px 0 0;
					justify-content: flex-start;
					a {
						font-size: 18px;
						@include aniverse_normal;
						color: #000;
						padding: 10px 15px;
						text-align: center;
						line-height: 23px;
						display: inline-block;
					}
				}
			}

			.col_right {
				padding: 54px 75px 54px;
				grid-area: banner_right;
			}
		}

		&--guide{
			font-size:18px;
			padding:30px 0;

			.grid{
				@media (min-width: $screen-sm) {
					grid-template-columns: 1fr;
					grid-column-gap: 30px;

					.banner.spec1 .grid {
					    grid-template-columns: minmax(300px,1fr) 2fr;
					}
				}

				.col_left{
					padding:0;
					justify-content: flex-start;

					.img{
						display: block;
						margin:0 auto;
						margin-bottom:30px;

						@media (min-width: $screen-sm) {
							margin-bottom:0;
						}
					}
				}
				.col_right{
					padding:30px;
					text-align:center;

					@media (min-width: $screen-sm) {
						padding:0;
						text-align:left;
					}

					ul{
						    display: inline-block;
					}
				}
			}
			.free_pass{
				background:#414042;
				padding: 10px 50px;

				h3{
					font-size: 20px;
				}
			}
			h2{
				margin-bottom:30px;
			}
			ul {
			  margin: 0 0 20px 20px;

			  li {
			      list-style: disc outside none;
			    }
			}


			ol {
			margin: 0 0 20px 20px;

			    li{
			        list-style: decimal outside;
			    }

			}
			.row{
				max-width: 1110px;
			}
		}
	}

	&.spec2 {
		padding: 0 0 22px;
		img {
			width: 100%;
		}
	}

	&.spec3 {
		position: relative;
		margin: 0 0 22px;
		img {
			position: absolute;
			z-index: -1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		.overlay {
		    background: rgba(0,0,0,.5);
		    height: 100%;
		    position: absolute;
		    width: 100%;
		    z-index: 1;
		}

		h1 {
			color: #fff;
			font-size: 40px;
			padding: 100px 0;
			position: relative;
			text-align: center;
			z-index: 7;
		}
	}
	&.spec4 {
		position: relative;
		margin: 0 0 22px;
		img {
			position: absolute;
			z-index: -1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		.overlay {
		    background: rgba(0,0,0,.5);
		    height: 100%;
		    position: absolute;
		    width: 100%;
		    z-index: 1;
		}

		.row {
			position: relative;
			z-index: 9;
			padding: 50px 20px;

			@media (min-width: $screen-sm) {
				padding: 100px 0;
			}
		}

		h1 {
			text-align: left;
			color: #fff;
			z-index: 9;
			position: relative;
			font-size: 40px;
		}

		p {
			font-size: 19px;
			color: #fff;
			padding: 0 0 35px;
		}

		.button_container {
			justify-content: flex-start;
		}
	}

	&.free_pass {
		padding: 0 0 12px;
		.block{
			position: relative;

			.overlay {
			    background: rgba(0,0,0,.5);
			    height: 100%;
			    left: 0;
			    position: absolute;
			    top: 0;
			    width: 100%;
			    z-index: 1;
			}
			.title_main {
				text-align: center;
				z-index: 8;
				h1 {
					background:none;
					color: #fff;
					float: none;
				}
			}
		}	


	}
}


.free_pass .post_content .section_title p{
	    font-family: aniversregular,sans-serif;
}