.contact_details {
	display: flex;
	flex-wrap: wrap;
	max-width: 400px;
	padding: 0 0 23px;
	width: 100%;
	div {
		font-size: 18px;
		max-width: 50%;
		padding: 0 0 2px;
		width: 100%;

		&:nth-child(2), &:last-child {
			text-align: right;
		}
	}
}

.contact_form {
	width: 100%;
	
	padding: 30px 0 0;

	@media (min-width: $screen-sm) {
		max-width: 54%;
	}
	fieldset {
		margin: 0;
		padding: 0;
		border: 0;
		.frm_hidden {
			display: none;
		}

		h3 {
			color: #000;
			@include aniverse_bold;
			font-size: 18px;
			text-transform: uppercase;
			text-align: left;
			padding: 0 0 18px;
		}

		.frm_description {
			padding: 0 0 19px;
			p {
				color: #fff;
				@include aniverse_normal;
				font-size: 14px;
				text-align: center;
			}
		}

		.frm_none_container {
			label {
				display: none;
			}
		}

		.form-field {
			padding: 0 0 6px;
			input {
				border: none;
				background: #fff;
				padding: 10px 14px;
				margin: 0;
				width: 100%;
				color: #fff;
				font-size: 16px;
				@include aniverse_normal;
				background: $blue1;

				&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
				  color: #fff;
				}
				&::-moz-placeholder { /* Firefox 19+ */
				  color: #fff;
				}
				&:-ms-input-placeholder { /* IE 10+ */
				  color: #fff;
				}
				&:-moz-placeholder { /* Firefox 18- */
				  color: #fff;
				}
			}

			textarea {
				background: $blue1;
				padding: 10px 14px;
				border: none;
				width: 100%;

				&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
				  color: #fff;
				}
				&::-moz-placeholder { /* Firefox 19+ */
				  color: #fff;
				}
				&:-ms-input-placeholder { /* IE 10+ */
				  color: #fff;
				}
				&:-moz-placeholder { /* Firefox 18- */
				  color: #fff;
				}
			}
		}

		.frm_submit {
			button,
			input[type=submit] {
				background: $blue1;
				border: none;
				color: #fff;
				@include aniverse_bold;
				font-size: 18px;
				text-transform: uppercase;
				border: 0;
				padding: 10px 53px;
				margin: 10px 0;
			}
		}
	}
}

      .checker{
        margin-right:10px;
        width:15px;
        height:15px;

        @media (max-width: $screen-sm) {
          font-size:12px;
        }

        input{
          width:15px;
        height:15px;
        }

        span{
          background-color:transparent;
          border:1px solid #768A77;
          width:15px;
          height:15px;

          &.checked{
            background-color: $blue1;
          }
        }
      }

#frm_form_8_container{
	.selectric {
		background:$blue1;

	    .label{
			color:white;
			background:$blue1;
			    margin: 0 38px 0 12px;
	    }
	    .button{
	    	color:white;
	    	background:$blue1;

	    	&:after {
			    border-top-color: white;
			}
	    }
	}
	.selectric-wrapper{
		background:$blue1;
	}
	.frm_error{
		padding: 5px 0;
    	color: #ff0000;
    	font-size: 12px;
	}
	.checker{
		    margin-top: -5px;
	}
}