.comment_wrapper {
	padding: 30px 0 60px;
	position: relative;

	.qt_l {
		position: absolute;
		float: left;
		left: 0;
		svg {
			width: 100%;
			height: auto;
		}
	}

	.qt_r {
		position: absolute;
		float: right;
		right: 0;
		bottom: 0px;
		svg {
			width: 100%;
			height: auto;
		}
	}
}

.comment_body {
	ul {
		margin: 0 !important;
		padding: 0;
		li {
			list-style: none !important;
			border-bottom: 1px solid $yellow1;
			margin: 0 0 25px;

			&:last-child {
				border-bottom: 0;
			}

			.comment_text {
				p {
					padding: 0 0 10px;
				}
			}
		}
	}
}

.comment_form {
	h3 {
	    color: #000;
	    font-family: aniversbold,sans-serif;
	    font-size: 18px;
	    font-style: normal;
	    font-weight: 400;
	    padding: 0 0 18px;
	    text-align: left;
	    text-transform: uppercase;
	}

	form {
		.form_row {
			padding: 0 0 6px;

			input {
			    border: none;
				background: #fff;
				padding: 10px 14px;
				margin: 0;
				width: 100%;
				color: #fff;
				font-size: 16px;
				@include aniverse_normal;
				background: $blue1;

				&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
				  color: #fff;
				}
				&::-moz-placeholder { /* Firefox 19+ */
				  color: #fff;
				}
				&:-ms-input-placeholder { /* IE 10+ */
				  color: #fff;
				}
				&:-moz-placeholder { /* Firefox 18- */
				  color: #fff;
				}
			}

			textarea {
				border: none;
				background: #fff;
				padding: 10px 14px;
				margin: 0;
				width: 100%;
				color: #fff;
				font-size: 16px;
				@include aniverse_normal;
				background: $blue1;

				&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
				  color: #fff;
				}
				&::-moz-placeholder { /* Firefox 19+ */
				  color: #fff;
				}
				&:-ms-input-placeholder { /* IE 10+ */
				  color: #fff;
				}
				&:-moz-placeholder { /* Firefox 18- */
				  color: #fff;
				}
			}

			input[type="submit"] {
			    background: #71c8c6;
			    border: none;
			    color: #fff;
			    font-family: aniversbold,sans-serif;
			    font-style: normal;
			    font-weight: 400;
			    font-size: 18px;
			    text-transform: uppercase;
			    border: 0;
			    padding: 10px 53px;
			    margin: 10px 0;
			    width: auto;
			}
		}
	}
}