.signup_col {
	padding: 60px 0 0;
	width: 100%;
	margin: 0 auto;
	@media (min-width: $screen-sm) {
		max-width: 780px;
	}

	h2 {
		@include aniverse_bold;
		color: #000;
		text-align: center;
		padding: 0 0 50px;

		@media (min-width: $screen-sm) {
			font-size: 36px;
		}

		span {
			color: #ffc90d;
		}
	}
}

.join_now_form {
	.frm_error_style {
		display: none;
	}
	.frm_submit {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 40px 0 0;
		button {
			background: #ffc90d !important;
			font-size: 23px !important;
			text-transform: uppercase  !important;
			border: none !important;
			font-family: 'ProximaNova-Bold'  !important;
			padding: 12px 27px !important;
			margin: 0 !important;
			border-radius: 0 !important;
			color: #000 !important;
		}
	}
	.frm_form_field {
		margin: 0 !important;
		&.purple {
			margin: 0 20px !important;
			position: relative;
			background:#ad0f93;
			@media (min-width: $screen-sm) {
				margin: 0 !important;
			}

			a.popup {
				position: absolute;
				float: right;
				right: 20px;
				top: 20px;
				width: 20px;
				height: 20px;

				@media (min-width: $screen-sm) {
					right: 20px;
					top: 20px;
					width: 30px;
					height: 30px;
				}

				&:before {
					content: "";
					width: 20px;
					height: 20px;
					background-image:url(../images/join_page/info_button.png);
					background-repeat: no-repeat;
					background-size: 100% auto;
					display: block;

					@media (min-width: $screen-sm) {
						width: 30px;
						height: 30px;
					}
				}
			}
		}

		.frm_error {
			padding: 25px 0;
			font-size: 18px;
			text-align: center;
		}

		&.select {
			width: 100%;
			max-width: 60%;
			margin: 0 auto !important;
			padding: 0 0 50px;
		}
	}
	.frm_opt_container {
		display: flex;
		align-items: center;
		flex-direction: column;
		@media (min-width: $screen-sm) {
			flex-direction: row;
		}

		.frm_radio {
			width: 100%;
			padding: 0 20px 20px;
			@media (min-width: $screen-sm) {
				max-width: 50%;
				padding: 0;
			}

			&:first-child {
				.form_header {
					border-top: 10px solid #939598;
					border-left: 10px solid #939598;
					border-right: 10px solid #939598;
				}
				.form_content {
					border-left: 10px solid #939598;
					border-right: 10px solid #939598;
				}
				.form_footer {
					border-left: 10px solid #939598;
					border-right: 10px solid #939598;
					border-bottom: 10px solid #939598;
				}
			}
			&:nth-child(2) {
				.form_header {
					border-top: 10px solid #ffc90d;
					border-left: 10px solid #ffc90d;
					border-right: 10px solid #ffc90d;
				}
				.form_content {
					border-left: 10px solid #ffc90d;
					border-right: 10px solid #ffc90d;
				}
				.form_footer {
					border-left: 10px solid #ffc90d;
					border-right: 10px solid #ffc90d;
					border-bottom: 10px solid #ffc90d;
				}
			}


			label {
				padding: 0 !important;
				text-indent: 0 !important;
				position: relative;
				display: block;

				&:hover {
					cursor: pointer;
				}
			}

			.grey-b {
				background:#939598;
				color: #fff;
			}
			.yellow-b {
				background:#ffc90d;
				color: #000;
			}

			
			input[type="radio"] {
				display: none !important;

			}
			input[type="radio"]:checked ~ {

				.form_header {
					border-top: 10px solid #000;
					border-left: 10px solid #000;
					border-right: 10px solid #000;
				}
				.form_content {
					border-left: 10px solid #000;
					border-right: 10px solid #000;
				}
				.form_footer {
					border-left: 10px solid #000;
					border-right: 10px solid #000;
					border-bottom: 10px solid #000;

					.footer_text {

						&.yellow-b {
							.box {
								position: relative;
								&:before {
						    		content: "";
						    		width: 7px;
						    		height: 13px;
						    		border-right: 1px solid #000;
						    		border-bottom: 1px solid #000;
						    		position: absolute;
						    		transform: rotate(45deg);
						    		left: 9px;
						    		top: 3px;
						    	}
							}
						}
						&.grey-b {
							.box {
								position: relative;
								&:before {
						    		content: "";
						    		width: 7px;
						    		height: 13px;
						    		border-right: 1px solid #fff;
						    		border-bottom: 1px solid #fff;
						    		position: absolute;
						    		transform: rotate(45deg);
						    		left: 9px;
						    		top: 3px;
						    	}
							}
						}
						
					}
				}
			}

			.form_ribbon {
				font-size: 36px;
				@include aniverse_bold;
				position: absolute;
				width: 150px;
				height: 150px;
				z-index: 9;
				color: #000;
				@media (min-width: $screen-sm) {
					width: 150px;
					height: 160px;
				}

				&.left {
					text-indent: -9999px;
					&:before {
						@include aniverse_bold;
						content: 'most flexible';
						float: left;
						font-size: 28px;
						left: -31px;
						position: absolute;
						text-indent: 0px;
						top: 39px;
						transform: rotate(-45deg);
						z-index: 9;

						@media (min-width: $screen-sm) {
							left: -24px;
							top: 42px;
							font-size: 24px;
						}
					}
					&:after {
						background-image: url(../images/join_page/rib_left.png);
						background-repeat: no-repeat;
						background-size: 100%;
						content: "";
						float: left;
						height: 100%;
						left: -15px;
						position: absolute;
						top: -8px;
						width: 100%;

						@media (min-width: $screen-sm) {
							left: -15px;
							top: -9px;
						}
					}
				}

				&.right {
					text-indent: -9999px;
					float: right;
					right: 0;
					&:before {
						content: 'best value';
						font-size: 28px;
						@include aniverse_bold;
						position: absolute;
						float: left;
						text-indent: 0px;
						z-index: 9;
						transform: rotate(45deg);
						right: -18px;
						top: 39px;

						@media (min-width: $screen-sm) {
							right: -8px;
							top: 42px;
							font-size: 24px;
						}
					}
					&:after {
						content: "";
						background-image: url(../images/join_page/rib_right.png);
						background-repeat: no-repeat;
						background-size: 100%;
						width: 100%;
						height: 100%;
						position: absolute;
						float: right;
						right: -15px;
						top: -8px;

						@media (min-width: $screen-sm) {
							right: -15px;
							top: -9px;
						}
					}
				}
			}
			.form_header {
				position: relative;
				
				.form_header_main {
					padding: 35px 0 0;
				}


				h2 {
					font-size: 36px;
					font-family: 'ProximaNova-Bold';
					font-weight: 400;
					text-align: center;
					padding: 0 0 15px;
					@media (min-width: $screen-sm) {
						min-height: 111px;
					}
				}
			}
			.form_content {
				.form_content_main {
					text-align: center;
					font-size: 72px;
					font-family: 'ProximaNova-Bold';
					font-weight: 400;
					position: relative;


					&:after {
						content: "";
						position: relative;
						float: right;
						right: 0;
						width: 40px;
						height: 40px;
					}

					span {
						font-size: 30px;
						font-family: 'ProximaNova-Bold';
						font-weight: 400;
						bottom: 14px;
						position: absolute;
						right: 45px;
						@media (min-width: $screen-sm) {
							font-size: 30px;
							right: 71px;
						}
					}
				}
			}
			.form_footer {
				.form_footer_main {
					padding: 0 20px;
					@media (min-width: $screen-sm) {
						padding: 0 50px 20px;
					}
					> div {
						display: flex;
						flex-direction: row;
						align-items: center;
						padding: 0 0 5px;
						.ico {
							width: 20px;
							height: 20px;
							background-repeat: no-repeat;
							background-size: 100% auto;
							background-position: center;
							@media (min-width: $screen-sm) {
								width: 20px;
								height: 20px;
							}

							&.key-w {
								background-image:url(../images/join_page/key_white.png);
							}
							&.cal-w {
								background-image:url(../images/join_page/cal_white.png);
							}
							&.wifi-w {
								background-image:url(../images/join_page/wifi_white.png);
							}
							&.arking-w {
								background-image:url(../images/join_page/parking_white.png);
							}

							&.key-b {
								background-image:url(../images/join_page/key_black.png);
							}
							&.cal-b {
								background-image:url(../images/join_page/cal_black.png);
							}
							&.wifi-b {
								background-image:url(../images/join_page/wifi_black.png);
							}
							&.arking-b {
								background-image:url(../images/join_page/parking_black.png);
							}
						}

						span {
							font-size: 21px;
							font-family: 'ProximaNova-Regular';
							font-weight: 300;
							padding: 0 0 0 25px;

							@media (min-width: $screen-sm) {
								font-size: 21px;
							}

						}
					}
				}
				.footer_text {
					padding: 0 20px 20px;
					display: flex;
					align-items: center;
					justify-content: center;
					flex-wrap: wrap;
					@media (min-width: $screen-sm) {
						padding: 0 50px 20px;
					}

					&:before {
						content: "";
						width: 100%;
						position: relative;
						float: left;
						height: 1px;
						background: #fff;
						margin: 0 0 30px;
					}

					&.yellow-b {

						&:before {
							background: #000;
						}
						.box {
							border: 1px solid #000;
						}
					}

					.box {
						width: 25px;
						height: 25px;
						border: 1px solid #fff;
					}

					.text {
						font-size: 21px;
						font-family: 'ProximaNova-Regular';
						padding: 0 0 0 25px;
					}
				}
			}
		}
	}
}

.purple {

	&.vertical_radio {
		display: flex;
		align-items: center;
		justify-content: center;

		.frm_opt_container {
			width: 100%;
			.frm_checkbox {
				width: 100%;

				h2, .price, .prem_benefits, .footer_text {
					width: 100%;
				}
				h2 {
					border-top: 10px solid #ad0f93;
					border-left: 10px solid #ad0f93;
					border-right: 10px solid #ad0f93;
				}

				.price {
					border-left: 10px solid #ad0f93;
					border-right: 10px solid #ad0f93;
				}

				.prem_benefits {
					border-left: 10px solid #ad0f93;
					border-right: 10px solid #ad0f93;
				}

				.footer_text {
					border-left: 10px solid #ad0f93;
					border-right: 10px solid #ad0f93;
					border-bottom: 10px solid #ad0f93;
				}
			}
		}

		label {
			font-size: 27px !important;
			color: #fff !important;
			text-indent: 0px !important;
			padding-left: 0 !important;
			display: flex;
			align-items: center;
			justify-content: center;

			&:hover {
				cursor: pointer;
			}

			input[type="checkbox"] {
				display: none !important;

			}

			input[type="checkbox"]:checked ~ {
				.premium_main {
					h2 {
						border-top: 10px solid #000;
						border-left: 10px solid #000;
						border-right: 10px solid #000;
					}

					.price {
						border-left: 10px solid #000;
						border-right: 10px solid #000;
					}

					.prem_benefits {
						border-left: 10px solid #000;
						border-right: 10px solid #000;
					}

					.footer_text {
						border-left: 10px solid #000;
						border-right: 10px solid #000;
						border-bottom: 10px solid #000;

						.box {
							&:before {
								content: "";
							}
						}
					}
				}
			}
			.premium_main {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				width: 100%;

				h2 {
					font-family: 'ProximaNova-Bold';
					font-size: 36px;
					color: #fff;
					padding: 30px 0 0;
				}

				.price {
					align-items: center;
					color: #fff;
					display: flex;
					flex-direction: column;
					font-family: 'ProximaNova-Bold';
					font-size: 36px;
					justify-content: center;
					padding: 40px 0;

					@media (min-width: $screen-sm) {
						flex-direction: row;
						padding: 0;
						padding: 15px 0;
					}

					div {
						font-size: 72px;
						@media (min-width: $screen-sm) {
							padding: 0 20px;
							font-size: 72px;
						}
						span {
							font-size: 30px;
							position: relative;
							bottom: 0px;
							@media (min-width: $screen-sm) {
								font-size: 40px;
							}
						}
					}
				}

				.prem_benefits {
					display: flex;
					align-items: flex-start;
					justify-content: center;
					flex-direction: column;
					padding: 0 0 0 20px;
					@media (min-width: $screen-sm) {
						flex-direction: row;
						align-items: center;
						padding: 0;
					}
					div {
						font-family: 'ProximaNova-Regular';
						font-size: 22px;
						color: #fff;
						display: flex;
						padding: 0 0 20px;
						
						@media (min-width: $screen-sm) {
							padding: 0 46px 0 0;
							font-size: 22px;
						}

						&:last-child {
							padding: 0;
						}


						.icon {
							width: 25px;
							height: 25px;
							background-size: 100%;
							background-repeat: none;
							margin: 0 25px 0 0;
							background-repeat: no-repeat;

							&.viva {
								background-image:url(../images/join_page/viva_heart.png);
							}
							&.snow {
								background-image:url(../images/join_page/snow.png);
							}
						}
					}
				}
				.footer_text {
					padding: 20px 0 30px;
					display: flex;
					justify-content: center;
					align-items: center;
					font-size: 18px;

					@media (min-width: $screen-sm) {
						font-size: 22px;
					}


					.box {
						width: 100%;
						max-width: 25px;
						height: 25px;
						border: 1px solid #fff;
						position: relative;
						display: block;
						margin: 0 20px 0;
						
						&:before {
				    		width: 6px;
				    		height: 14px;
				    		border-right: 1px solid #fff;
				    		border-bottom: 1px solid #fff;
				    		position: absolute;
				    		transform: rotate(45deg);
				    		left: 9px;
				    		top: 2px;
				    	}
					}
				}
			}
		}
	}
}