.btn_close {
    display: none;
    float: right;
    position: relative;

    &.show {
        display: block;
        padding: 9px 0 0 0;
    }
}
.bar1, .bar2, .bar3 {
    background-color: #fff;
    height: 3px;
    margin: 5px 0;
    transition: 0.4s;
    width: 26px;
}

.show {
    .bar1 {
        -webkit-transform: rotate(-45deg) translate(0px, 0px) ;
        transform: rotate(-45deg) translate(0px, 0px) ;
    }
    .bar2 {
        opacity: 0;
    }
    .bar3 {
        -webkit-transform: rotate(45deg) translate(-5px, -5px) ;
        transform: rotate(45deg) translate(-5px, -5x) ;
    }
}

.mobi_ico {
    display: flex;
    padding: 11px 15px 12px;

    .menu_ico {
        padding-left: 15px;
    }

    .btn_menu {
        display: block;

        &.hide {
            display: none;
        }
    }
    .btn_close {

    }

    @media (min-width: $screen-md) {
        display: none;
    }
}
.section {
	&.header {
		background: #000;
        border-bottom: 2px solid #fff;
        position: relative;
        z-index: 9999;

		.row {
			display: flex;
		    align-items: center;
    		justify-content: space-between;
            position: relative;

            .mega{
                display: none;

                @media (min-width: $screen-md) {
                    display: block;
                    width:100%;
                    position: absolute;
                    top:calc(100% - 40px);
                    left:0;
                    -webkit-box-shadow: 0px 5px 14px -3px rgba(0,0,0,0.18);
                    -moz-box-shadow: 0px 5px 14px -3px rgba(0,0,0,0.18);
                    box-shadow: 0px 5px 14px -3px rgba(0,0,0,0.18);
                    max-height:0;
                    height:auto;
                    overflow:hidden;
                    transition: max-height 0.5s;

                    .inner{
                        padding-top:40px;
                        display: block;
                        height:100%;
                    }
                    &__content{
                        background: white;
                        height:100%;
                        display: block;
                        padding: 30px 50px;
                    }
                }

                .gridlist{

                    .item{
                        display: inline-block;
                        padding:10px;
                        float:left;
                        width:33.3%;
                        text-decoration:none;
                        color:black;

                        img{
                            float:left;
                        }
                        .item_content{
                            overflow:hidden;
                            padding-left:20px;

                            h3{
                                font-size: 15px;
                                margin-bottom: 10px;

                                &:after {
                                    background: url(../images/svg/arrow_right.svg) no-repeat;
                                    content: "";
                                    float: right;
                                    height: 10px;
                                    position: absolute;
                                    right: 0;
                                    top: 14px;
                                    width: 10px;
                                }
                            }
                            p{

                            }
                            br{
                                display:none;
                            }
                        }
                    }
                }

                .megafitnesslibrary{

                    h3{
                        display: block;
                        clear:both;
                        margin-bottom:15px;
                        font-size:20px;
                    }
                    .left{
                        display: flex;
                        flex-direction:column;
                        float:left;
                        width:30%;
                        padding-right:30px;
                        height: 240px;

                        a{
                            margin-top:auto;
                            display: block;
                            text-align:left;
                            padding: 10px;
                            font-weight: bold;
                            color: black;

                            &:after {
                                    background: url(../images/svg/arrow_right.svg) no-repeat;
                                    content: "";
                                    float: right;
                                    height: 10px;
                                    position: absolute;
                                    right: 5px;
                                    top: 14px;
                                    width: 10px;
                                }


                        }

                    }
                    .right{
                        display: block;
                        float:left;
                        width:70%;
                        padding-left:30px;
                        border-left:1px solid black;
                            height: 240px;

                        h3{
                            display: block;
                            clear:both;
                        }

                        .item{
                        display: inline-block;
                        padding: 0 20px 20px 0;
                        float:left;
                        width:50%;
                        text-decoration:none;
                        color:black;
                            height: 120px;


                        img{
                            float:left;
                        }
                        .item_content{
                            overflow:hidden;
                            padding-left:20px;

                            h3{
                                font-size: 15px;
                                margin-bottom: 10px;

                                &:after {
                                    background: url(../images/svg/arrow_right.svg) no-repeat;
                                    content: "";
                                    float: right;
                                    height: 10px;
                                    position: absolute;
                                    right: 6px;
                                    top: 3px;
                                    width: 10px;
                                }
                            }
                            p{

                            }
                        }
                    }
                    }
                }

                .megaclasses{

                        a{
                            color:black;
                            padding-bottom:5px;
                        }
                    .header{
                        display: block;
                        padding-bottom:30px;
                        border-bottom:1px solid #ccc;



                        ul{
                            li{
                                display: inline-block;
                                margin-right:10px;

                                strong{
                                    text-transform:uppercase;
                                }

                                a{
                                    

                                &:after {
                                    background: url(../images/svg/arrow_right.svg) no-repeat;
                                    content: "";
                                    float: right;
                                    height: 10px;
                                    position: absolute;
                                    right: 0;
                                    top: 5px;
                                    width: 10px;
                                }  
                                }
                            }
                        }
                    }
                    .footer{
                        display: block;
                        padding-top:30px;
                        font-size:12px;

                        ul{
                            li{
                                display: inline-block;
                                margin-right:15px;
                                text-align:center;
                                width:90px;

                                img{
                                    display: block;
                                    margin-bottom:5px;
                                }
                            }
                        }
                    }
                }

                .row{
                    display: flex;
                    flex-direction:row;
                        align-items: flex-start;
                }

                .left{
                    width:150px;
                    padding-right:30px;
                }
                .right{
                    width:calc(100% - 150px);
                    padding-left:30px;
                }
                .imgs{
                    padding-top:20px;

                        a{
                            float:left;
                            padding-left:0;
                        }
                    }
                header{
                    text-align:left;

                    a{
                        display: inline-block !important;
                        padding:0!important;
                    }
                }
                .clubsnav{
                    border-right:1px solid black;
                        min-height: 217px;

                    .selectric{
                        border:0;
                        margin-bottom:10px;



                        .label{
                            margin-left: 0;
                            font-family: anivers,sans-serif;
                            font-style: normal;
                            font-weight: 700;
                            color:black;
                        }
                    }
                    .selectric-scroll{
                        li{
                            padding:5px;
                        }
                    }

                    ul{
                        li{
                            align-items:left;
                            a{
                                padding: 3px 0;
                                font-family: anivers,sans-serif;
                                font-style: normal;
                                font-weight: 700;
                                color:black;
                            }
                        }
                    }
                }

                &--specials{

                    .mega__content{
                        display:flex;
                        flex-direction:row;
                    }

                    .gridlist{
                        width:calc(100% - 350px);

                        a.item{
                            width:50%;
                        }
                    }
                    .mega__pic{
                        width:350px;

                        img{
                            margin-left: auto;
                        }
                    }
                }

                &--autoheight{
                    //height:auto;
                }
            }

            .site_logo {
                padding: 10px;

                svg {
                    height: 54px;
                    width: auto;
                }
                @media (min-width: $screen-md) {

                }
            }

            .mobi_join {
                @media (min-width: $screen-md) {
                    display: none;
                }

                a {
                    font-family: anivers,sans-serif;
                    font-style: normal;
                    font-weight: 700;
                    border: 2px solid #fff;
                    color: #000;
                    font-size: 20px;
                    justify-content: center;
                    padding: 11px 7px;
                    text-align: center;
                    min-width: 150px;
                }
            }

            .nav_section {
                align-items: center;
                justify-content: space-between;
                display: none;
                width: 100%;

                  @media (min-width: $screen-md) {
                    width: auto;
                    display: flex;

                  }

                &.show {
                    display: block;
                    position: absolute;
                    background:rgba(0,0,0,0.8);
                    top: 72px;
                    z-index: 9;
                    left: 0;
                }
            }

    		.menu {
                width: 100%;
                padding: 20px 0 0;

                @media (min-width: $screen-md) {
                    display: flex;
                    width: auto;
                    padding: 0;
                }

                
    			li {
                    display: flex;
                    flex-direction: column;
                    //position: relative;
                    padding: 10px;
                    border-bottom: 1px solid #fff;
                    @media (min-width: $screen-md) {
                        align-items: center;
                        padding: 0;
                        border-bottom: 0;

                        &.hasmega:hover{
                            > a.link:after{
                               width: 0;
                               height: 0;
                               border-left: 20px solid transparent;
                               border-right: 20px solid transparent;
                               border-bottom: 20px solid #fff;
                               bottom: -40px;
                               background: none;
                           }
                        }
                    }
    				a {
                        display: flex;
                        flex-direction: row;
                        text-decoration: none;
                        align-items: center;
                        padding: 0 15px;
                        position: relative;

                        @media (min-width: $screen-md) {
                            flex-direction: column;
                        }

                        .menu_ico {
                            width: 37px;
                            height: 29px;
                            margin: 0 10px 0 0;
                            text-align: center;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            @media (min-width: $screen-md) {
                                flex-direction: column;
                                margin: 0 0 -4px;
                                display: block;
                            }

                            svg {
                                // width: 100%;
                                // height: auto;
                            }
                        }
    					.menu_title {
    						color: $yellow1;
                            @include aniverse_bold;
                            font-size: 18px;
                            text-align: center;

                            span {
                                color: #fff;
                                font-weight: normal;
                            }
    					}
    				}

                    &.current-menu-item {
                        a.link:after {
                            content: "";
                            background: url(../images/svg/up_arrow.svg) no-repeat;
                            width: 13px;
                            height: 6px;
                            position: absolute;
                            bottom: 22px;
                            transform: rotate(90deg);

                            @media (min-width: $screen-md) {
                                bottom: -14px;
                                transform: none;
                            }
                        }
                    }

                    &:hover {
                        a.link:after {
                            content: "";
                            background: url(../images/svg/up_arrow.svg) no-repeat;
                            width: 13px;
                            height: 6px;
                            position: absolute;
                            bottom: 22px;
                            transform: rotate(90deg);

                            @media (min-width: $screen-md) {
                                bottom: -14px;
                                transform: none;
                            }
                        }

                        .mega{
                            max-height:600px;

                            // height:340px;

                            // &--extraheight{
                            //    height: 410px;
                            // }
                            // &--onerow{
                            //     height:220px;
                            // }
                        }
                    }
    			}
    		}

            .member_nav {
                display: flex;
                flex-wrap: wrap;
                padding: 24px 0 0 15px;
                justify-content: flex-end;
                max-width: 210px;

                @media (min-width: $screen-md) {
                    max-width: 165px;
                }

                a {
                    @include aniverse_normal;
                    color: #fff;
                    display: flex;
                    font-size: 18px;
                    text-transform: uppercase;
                    margin: 0 0 19px;
                    align-items: center;

                    @media (min-width: $screen-md) {
                        font-size: 10px;
                    }

                    &:first-child {
                        padding: 0 27px 0 0;
                    }

                    &:nth-child(2) {
                        justify-content: flex-end;
                    }

                    svg {
                        margin: 0 20px 0 0;
                        height: 15px;
                        width: auto;
                    }


                    &.big_button {
                        @include aniverse_bold;
                        border: 2px solid #fff;
                        color: #000;
                        font-size: 20px;
                        justify-content: center;
                        padding: 11px 0;
                        text-align: center;
                        min-width: 150px;
                        display: none;

                        @media (min-width: $screen-md) {
                            display: block;
                        }
                    }


                }
            }
		}
	}
}

body.sticky{
    .section.header{
        position:fixed;
        width:100%;
    }
}