.guidetop{
	display: flex;
	flex-direction: column;

		@media (min-width: $screen-sm) {
			flex-direction: row;
		}

	.left{
		display: block;
		width:100%;
		padding-right:0;

		@media (min-width: $screen-sm) {
			width:40%;
			padding-right:30px;
		}

		img{
			max-width:60%;
			display: block;
			margin:auto;

			@media (min-width: $screen-sm) {
				max-width:100%;
			}
		}
		.img{
			width:100%;
		}
	}
	.right{
		display: block;
		width:100%;
		padding: 30px;
		text-align:center;

		@media (min-width: $screen-sm) {
			width:60%;
			padding:0;
			text-align:left;
		}

		form{
			input{
				text-align:center;
			}
		}
		.select{
			text-align:center;

			option,span{
				text-align:center;
			}
		}
		.frm_error{
			color:white;
			font-size:12px;
			padding: 5px 0;
		}
		ul{
			display: inline-block;

			@media (min-width: $screen-sm) {
				display: block;
			}
		}
	}


	.selectric .label{
		margin: 0 10px;
	}
}